import { Component } from '@angular/core';

import { Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { AccountStatus } from '@model';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-account-view',
  templateUrl: './account-view.component.html'
})
export class AccountViewComponent {

  returnToLink$: Observable<string>;

  constructor(sessionService: SessionService) {
    this.returnToLink$ = sessionService.currentCompany$.pipe(
      map(company =>
        company && company.status === AccountStatus.Open ||
        company && company.status === AccountStatus.Test ? WellKnownRoutes.Dashboard : null)
    );
  }

  back() {
    // WARNING: A deep-link directly here means that history.back() could leave the site!
    // TODO: Guard against this.
    // Could track if this was the first navigation. If so, "back" should go somewhere safe
    // Tapping a router.events navigation end somewhere (e.g, TitleService)
    // would be a place to capture if this was first nav or a later nav
    window.history.back();
  }
}
