import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AccountViewComponent } from './account-view.component';
import { AccountDetailsModule } from '@app/account-details/account-details.module';
import { SharedModule } from '@shared';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: AccountViewComponent
  },
];
@NgModule({
  imports: [SharedModule, AccountDetailsModule, RouterModule.forChild(routes)],
  declarations: [ AccountViewComponent ]
})
export class AccountViewModule {}
